}
.cancelPost {
    position: absolute;
    bottom: 110px;
    right: 330px;
    width: 80px;
    height: 30px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    border: none;
    border-radius: 30px;
    background: #E9D7E4;
    -webkit-box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
            box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    color: #50394B;
    font-family: Inter;
    font-size: 16px;
